
export default {
  inheritAttrs: true,
  name: "exerciseCard",
  props: {
    index: {
      type: Number,
      default: 0
    },
    exercise: {
      type: Object,
      default: () => {
      },
    },
    'disable': {
      type: Boolean,
      default: false
    },
    'programview': {
      type: Boolean,
      default: false
    }
  },
  computed: {
    nuxtloading() {
      return $nuxt.$loading.loading
    }
  },
  methods: {
    pickMe() {
      this.$analytics.logEvent('exercise_opened', {
        "id": this.exercise.id,
        "type": this.exercise.type,
        "name": this.exercise.title,
        "program_id": typeof this.exercise.program_id !== 'undefined' ? this.exercise.program_id : null
      })
      this.$emit('click', this.exercise)
    }
  }
}
